import axios from 'axios';
import qs from 'qs';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

import { Message } from 'element-ui';

//  cookie
// axios.defaults.withCredentials = true;

//  axios 
const service= axios.create({
   // 
    baseURL: 'http://localhost:5200',
   //  5s
    timeout: 5000
});

// token
service.interceptors.request.use(config => {
    // axiosPOSTOPTIONS
    if(config.method  === 'post'){
        config.data = qs.stringify(config.data);
    }
    let admin=config.url.slice(0,6);  //console.log("admin:",admin);
    if (localStorage.getItem('token') && admin==='/admin') {
        config.data+='&token='+localStorage.getItem('token'); //token
        // console.log(config.data);
    }
    NProgress.start();//  
    return config;  //  config

}, error => {//   
    console.log(error);
    return Promise.reject();
});

// ,400500
service.interceptors.response.use(
    response => {
        if (response.status === 200) {
            // console.log("response",response);
            try {
                if(response.data.data[0].code===10099){//token
                    Message.error({
                        message: '!'
                    });
                    setTimeout(() => {
                        localStorage.removeItem("token");  
                        location.reload();
                    }, 1000);
                   
                }
            } catch (error) {
               console.log(error); 
               
            }
           
            NProgress.done();//
            return Promise.resolve(response.data);
        } else {
            return Promise.reject(response);
        }
    },
    // 200,
    error => {
        if (  400 <= error.response.status <500) {
            console.error(error.response.status)
            // alert("");
            // token
            // localStorage.removeItem("token");
            // 
            //setTimeout(() => {
            //   window.location.href = "/login";
            //}, 1000);
        } else {
            if (error.response.status >= 500) {
                alert("");
            } else {
                alert("");
                return Promise.reject(error)
            }
        }
    }
);
